export const oblomov = `Вошел человек лет сорока, принадлежащий к крупной породе, высокий, объемистый в плечах и во всем туловище, с крупными чертами лица, с большой головой, с крепкой, коротенькой шеей, с большими навыкате глазами, толстогубый. Беглый взгляд на этого человека рождал идею о чем-то грубом и неопрятном. Видно было, что он не гонялся за изяществом костюма. Не всегда его удавалось видеть чисто обритым. Но ему, по-видимому, это было все равно; он не смущался от своего костюма и носил его с каким-то циническим достоинством. Это был Михей Андреевич Тарантьев, земляк Обломова. Тарантьев смотрел на все угрюмо, с полупрезрением, с явным недоброжелательством ко всему окружающему, готовый бранить все и всех на свете, как будто какой-нибудь обиженный несправедливостью или непризнанный в каком-то достоинстве, наконец как гонимый судьбою сильный характер, который недобровольно, неуныло покоряется ей. Движения его были смелы и размашисты; говорил он громко, бойко и почти всегда сердито; если слушать в некотором отдалении, точно будто три пустые телеги едут по мосту. Никогда не стеснялся он ничьим присутствием и в карман за словом не ходил и вообще постоянно был груб в обращении со всеми, не исключая и приятелей, как будто давал чувствовать, что, заговаривая с человеком, даже обедая или ужиная у него, он делает ему большую честь. Тарантьев был человек ума бойкого и хитрого; никто лучше его не рассудит какого-нибудь общего житейского вопроса или юридического запутанного дела: он сейчас построит теорию действий в том или другом случае и очень тонко подведет доказательства, а в заключение еще почти всегда нагрубит тому, кто с ним о чем-нибудь посоветуется. Между тем сам как двадцать пять лет назад определился в какую-то канцелярию писцом, так в этой должности и дожил до седых волос. Ни ему самому и никому другому и в голову не приходило, чтоб он пошел выше. Дело в том, что Тарантьев мастер был только говорить; на словах он решал все ясно и легко, особенно что касалось других; но как только нужно было двинуть пальцем, тронуться с места — словом, применить им же созданную теорию к делу и дать ему практический ход, оказать распорядительность, быстроту, — он был совсем другой человек: тут его не хватало — ему вдруг и тяжело делалось, и нездоровилось, то неловко, то другое дело случится, за которое он тоже не примется, а если и примется, так не дай бог что выйдет. Точно ребенок: там не доглядит, тут не знает каких-нибудь пустяков, там опоздает и кончит тем, что бросит дело на половине или примется за него с конца и так все изгадит, что и поправить никак нельзя, да еще он же потом и браниться станет. Отец его, провинциальный подьячий старого времени, назначал было сыну в наследство искусство и опытность хождения по чужим делам и свое ловко пройденное поприще служения в присутственном месте; но судьба распорядилась иначе. Отец, учившийся сам когда-то по-русски на медные деньги, не хотел, чтоб сын его отставал от времени, и пожелал поучить чему-нибудь, кроме мудреной науки хождения по делам. Он года три посылал его к священнику учиться по-латыни. Способный от природы мальчик в три года прошел латынскую грамматику и синтаксис и начал было разбирать Корнелия Непота, но отец решил, что довольно и того, что он знал, что уж и эти познания дают ему огромное преимущество над старым поколением и что, наконец, дальнейшие занятия могут, пожалуй, повредить службе в присутственных местах. Шестнадцатилетний Михей, не зная, что делать с своей латынью, стал в доме родителей забывать ее, но зато, в ожидании чести присутствовать в земском или уездном суде, присутствовал пока на всех пирушках отца, и в этой-то школе, среди откровенных бесед, до тонкости развился ум молодого человека. Он с юношескою впечатлительностью вслушивался в рассказы отца и товарищей его о разных гражданских и уголовных делах, о любопытных случаях, которые проходили через руки всех этих подьячих старого времени. Но все это ни к чему не повело. Из Михея не выработался делец и крючкотворец, хотя все старания отца и клонились к этому и, конечно, увенчались бы успехом, если б судьба не разрушила замыслов старика. Михей действительно усвоил себе всю теорию отцовских бесед, оставалось только применить ее к делу, но за смертью отца он не успел поступить в суд и был увезен в Петербург каким-то благодетелем, который нашел ему место писца в одном департаменте, да потом и забыл о нем. Так Тарантьев и остался только теоретиком на всю жизнь. В петербургской службе ему нечего было делать с своею латынью и с тонкой теорией вершать по своему произволу правые и неправые дела; а между тем он носил и сознавал в себе дремлющую силу, запертую в нем враждебными обстоятельствами навсегда, без надежды на проявление, как бывали запираемы, по сказкам, в тесных заколдованных стенах духи зла, лишенные силы вредить. Может быть, от этого сознания бесполезной силы в себе Тарантьев был груб в обращении, недоброжелателен, постоянно сердит и бранчив. Он с горечью и презрением смотрел на свои настоящие занятия: на переписыванье бумаг, на подшиванье дел и т. п. Ему вдали улыбалась только одна последняя надежда: перейти служить по винным откупам. На этой дороге он видел единственную выгодную замену поприща, завещанного ему отцом и не достигнутого. А в ожидании этого готовая и созданная ему отцом теория деятельности и жизни, теория взяток и лукавства, миновав главное и достойное ее поприще в провинции, применилась ко всем мелочам его ничтожного существования в Петербурге, вкралась во все его приятельские отношения за недостатком официальных. Он был взяточник в душе, по теории, ухитрялся брать взятки, за неимением дел и просителей, с сослуживцев, с приятелей, бог знает как и за что — заставлял, где и кого только мог, то хитростью, то назойливостью, угощать себя, требовал от всех незаслуженного уважения, был придирчив. Его никогда не смущал стыд за поношенное платье, но он не чужд был тревоги, если в перспективе дня не было у него громадного обеда, с приличным количеством вина и водки. От этого он в кругу своих знакомых играл роль большой сторожевой собаки, которая лает на всех, не дает никому пошевелиться, но которая в то же время непременно схватит на лету кусок мяса, откуда и куда бы он ни летел. Таковы были два самые усердные посетителя Обломова. Зачем эти два русские пролетария ходили к нему? Они очень хорошо знали зачем: пить, есть, курить хорошие сигары. Они находили теплый, покойный приют и всегда одинаково если не радушный, то равнодушный прием. Но зачем пускал их к себе Обломов — в этом он едва ли отдавал себе отчет. А кажется, затем, зачем еще о сю пору в наших отдаленных Обломовках, в каждом зажиточном доме толпился рой подобных лиц обоего пола, без хлеба, без ремесла, без рук для производительности и только с желудком для потребления, но почти всегда с чином и званием. Есть еще сибариты, которым необходимы такие дополнения в жизни: им скучно без лишнего на свете. Кто подаст куда-то запропастившуюся табакерку или поднимет упавший на пол платок? Кому можно пожаловаться на головную боль с правом на участие, рассказать дурной сон и потребовать истолкования? Кто почитает книжку на сон грядущий и поможет заснуть? А иногда такой пролетарий посылается в ближайший город за покупкой, поможет по хозяйству — не самим же мыкаться! Тарантьев делал много шума, выводил Обломова из неподвижности и скуки. Он кричал, спорил и составлял род какого-то спектакля, избавляя ленивого барина самого от необходимости говорить и делать. В комнату, где царствовал сон и покой, Тарантьев приносил жизнь, движение, а иногда и вести извне. Обломов мог слушать, смотреть, не шевеля пальцем, на что-то бойкое, движущееся и говорящее перед ним. Кроме того, он еще имел простодушие верить, что Тарантьев в самом деле способен посоветовать ему что-нибудь путное. Посещения Алексеева Обломов терпел по другой, не менее важной причине. Если он хотел жить по-своему, то есть лежать молча, дремать или ходить по комнате, Алексеева как будто не было тут: он тоже молчал, дремал или смотрел в книгу, разглядывал с ленивой зевотой до слез картинки и вещицы. Он мог так пробыть хоть трои сутки. Если же Обломову наскучивало быть одному и он чувствовал потребность выразиться, говорить, читать, рассуждать, проявить волнение, — тут был всегда покорный и готовый слушатель и участник, разделявший одинаково согласно и его молчание, и его разговор, и волнение, и образ мыслей, каков бы он ни был. Другие гости заходили не часто, на минуту, как первые три гостя; с ними со всеми все более и более порывались живые связи. Обломов иногда интересовался какою-нибудь новостью, пятиминутным разговором, потом, удовлетворенный этим, молчал. Им надо было платить взаимностью, принимать участие в том, что их интересовало. Они купались в людской толпе; всякий понимал жизнь по-своему, как не хотел понимать ее Обломов, а они путали в нее и его: все это не нравилось ему, отталкивало его, было ему не по душе. `
