export const karenina = `Когда Облонский спросил у Левина, зачем он, собственно, приехал, Левин покраснел и рассердился на себя за то, что покраснел, потому что он не мог ответить ему: «Я приехал сделать предложение твоей свояченице», хотя он приехал только за этим. Дома Левиных и Щербацких были старые дворянские московские дома и всегда были между собою в близких и дружеских отношениях. Связь эта утвердилась еще больше во время студенчества Левина. Он вместе готовился и вместе поступил в университет с молодым князем Щербацким, братом Долли и Кити. В это время Левин часто бывал в доме Щербацких и влюбился в дом Щербацких. Как это ни странно может показаться, но Константин Левин был влюблен именно в дом, в семью, в особенности в женскую половину семьи Щербацких. Сам Левин не помнил своей матери, и единственная его сестра его была старше его, так что в доме Щербацких он в первый раз увидел ту самую среду старого дворянского, образованного и честного семейства, которой он был лишен смертью отца и матери. Все члены этой семьи, в особенности женская половина, представлялись ему покрытыми какою-то таинственною, поэтическою завесой, и он не только не видел в них никаких недостатков, но под этой поэтическою, покрывавшею их завесой предполагал самые возвышенные чувства и всевозможные совершенства. Для чего этим трем барышням нужно было говорить через день по-французски и по-английски; для чего они в известные часы играли попеременкам на фортепиано, звуки которого всегда слышались у брата наверху, где занимались студенты; для чего ездили эти учителя французской литературы, музыки, рисованья, танцев; для чего в известные часы все три барышни подъезжали в коляске к Тверскому бульвару в своих атласных шубках — Долли в длинной, Натали в полудлинной, а Кити совершенно в короткой, так что статные ножки ее в туго натянутых красных чулках были на всем виду; для чего им, в сопровождении лакея с золотою кокардой на шляпе, нужно было ходить по Тверскому бульвару, — всего этого и многого другого, что делалось в их таинственном мире, он не понимал, но знал, что все, что там делалось, было прекрасно, и был влюблен именно в эту таинственность совершавшегося. Во время своего студенчества он чуть было не влюбился в старшую, Долли, но ее вскоре выдали замуж за Облонского. Потом он начал было влюбляться во вторую. Он как будто чувствовал, что ему надо влюбиться в одну из сестер, только не мог разобрать, в какую именно. Но и Натали, только что показалась в свет, вышла замуж за дипломата Львова. Кити еще была ребенок, когда Левин вышел из университета. Молодой Щербацкий, поступив в моряки, утонул в Балтийском море, и сношения Левина с Щербацкими, несмотря на дружбу его с Облонским, стали более редки. Но когда в нынешнем году, в начале зимы, Левин приехал в Москву после года в деревне и увидел Щербацких, он понял, в кого из трех ему действительно суждено было влюбиться. Казалось бы, ничего не могло быть проще того, чтобы ему, хорошей породы, скорее богатому, чем бедному человеку, тридцати двух лет сделать предложение княжне Щербацкой; по всем вероятиям, его тотчас признали бы хорошею партией. Но Левин был влюблен, и поэтому ему казалось, что Кити была такое совершенство во всех отношениях, такое существо превыше всего земного, а он такое земное низменное существо, что не могло быть и мысли о том, чтобы другие и она сама признали его достойным ее. Пробыв в Москве, как в чаду, два месяца, почти каждый день видаясь с Кити в свете, куда он стал ездить, чтобы встречаться с нею, Левин внезапно решил, что этого не может быть, и уехал в деревню. Убеждение Левина в том, что этого не может быть, основывалось на том, что в глазах родных он невыгодная, недостойная партия для прелестной Кити, а сама Кити не может любить его. В глазах родных он не имел никакой привычной, определенной деятельности и положения в свете, тогда как его товарищи теперь, когда ему было тридцать два года, были уже — который полковник и флигель-адъютант, который профессор, который почтенный предводитель — директор банка и железных дорог или председатель присутствия, как Облонский; он же (он знал очень хорошо, каким он должен был казаться для других) был помещик, занимающийся разведением коров, стрелянием дупелей и постройками, то есть бездарный малый, из которого ничего не вышло, и делающий, по понятиям общества, то самое, что делают никуда не годившиеся люди. Сама же таинственная прелестная Кити не могла любить такого некрасивого, каким он считал себя, человека, и, главное, такого простого, ничем не выдающегося человека. Кроме того, его прежние отношения к Кити — отношения взрослого к ребенку, вследствие дружбы с ее братом, — казались ему еще новою преградой для любви. Некрасивого, доброго человека, каким он себя считал, можно, полагал он, любить как приятеля, но, чтобы быть любимым тою любовью, какою он любил Кити, нужно было быть красавцем, а главное — особенным человеком. Слыхал он, что женщины любят часто некрасивых, простых людей, но не верил этому, потому что судил по себе, так как сам он мог любить только красивых, таинственных и особенных женщин. Но, пробыв два месяца один в деревне, он убедился, что это не было одно из тех влюблений, которые он испытывал в первой молодости; что чувство это не давало ему минуты покоя; что он не мог жить, не решив вопроса: будет или не будет она его женой; и что его отчаяние происходило только от его воображения, что он не имеет никаких доказательств в том, что ему будет отказано. И он приехал теперь в Москву с твердым решением сделать предложение и жениться, если его примут. Или он не мог думать о том, что с ним будет, если ему откажут. Приехав с утренним поездом в Москву, Левин остановился у своего старшего брата по матери Кознышева и, переодевшись, вошел к нему в кабинет, намереваясь тотчас же рассказать ему, для чего он приехал, и просить его совета: но брат был не один. У него сидел известный профессор философии, приехавший из Харькова, собственно, затем, чтобы разъяснить недоразумение, возникшее между ними по весьма важному философскому вопросу. Профессор вел жаркую полемику против материалистов, а Сергей Кознышев с интересом следил за этою полемикой и, прочтя последнюю статью профессора, написал ему в письме свои возражения; он упрекал профессора за слишком большие уступки материалистам. И профессор тотчас же приехал, чтобы столковаться. Речь шла о модном вопросе: есть ли граница между психическими и физиологическими явлениями в деятельности человека и где она? Сергей Иванович встретил брата своего обычною для всех ласково-холодною улыбкой и, познакомив его с профессором, продолжал разговор. Маленький желтый человечек в очках, с узким лбом, на мгновение отвлекся от разговора, чтобы поздороваться, и продолжал речь, не обращая внимания на Левина. Левин сел в ожидании, когда уедет профессор, но скоро заинтересовался предметом разговора. Левин встречал в журналах статьи, о которых шла речь, и читал их, интересуясь ими, как развитием знакомых ему, как естественнику, по университету основ естествознания, но никогда не сближал этих научных выводов о происхождении человека как животного, о рефлексах, о биологии и социологии с теми вопросами о значении жизни и смерти для себя самого, которые в последнее время чаще и чаще приходили ему на ум. `
