export const asya = `Гагин встретил меня по-приятельски, осыпал меня ласковыми упреками; но Ася, точно нарочно, как только увидала меня, расхохоталась без всякого повода и, по своей привычке, тотчас убежала. Гагин смутился, пробормотал ей вслед, что она сумасшедшая, попросил меня извинить ее. Признаюсь, мне стало очень досадно на Асю; уж и без того мне было не по себе, а тут опять этот неестественный смех, эти странные ужимки. Я, однако, показал вид, будто ничего не заметил, и сообщил Гагину подробности моего небольшого путешествия. Он рассказал мне, что делал в мое отсутствие. Но речи наши не клеились; Ася входила в комнатку и убегала снова; я объявил наконец, что у меня есть спешная работа и что мне пора вернуться домой. Гагин сперва меня удерживал, потом, посмотрев на меня пристально, вызвался провожать меня. В передней Ася вдруг подошла ко мне и протянула мне руку; я слегка пожал ее пальцы и едва поклонился ей. Мы вместе с Гагиным переправились через Рейн и, проходя мимо любимого моего ясеня с статуйкой мадонны, присели на скамью, чтобы полюбоваться видом. Замечательный разговор произошел тут между нами. Сперва мы перекинулись немногими словами, потом замолкли, глядя на светлую реку. — Скажите, — начал вдруг Гагин, с своей обычной улыбкой, — какого вы мнения об Асе? Не правда ли, она должна казаться вам немного странной? — Да, — ответил я не без некоторого недоумения. Я не ожидал, что он заговорит о ней. — Ее надо хорошенько узнать, чтобы о ней судить, — промолвил он, — у ней сердце очень доброе, но голова бедовая. Трудно с нею ладить. Впрочем, ее нельзя винить, и если б вы знали ее историю. — Ее историю?. — перебил я, — разве она не ваша. Гагин взглянул на меня. — Уж не думаете ли вы, что она не сестра мне?. Нет, — продолжал он, не обращая внимания на мое замешательство, — она точно мне сестра, она дочь моего отца. Выслушайте меня. Я чувствую к вам доверие и расскажу вам всё. Отец мой был человек весьма добрый, умный, образованный — и несчастливый. Судьба обошлась с ним не хуже, чем со многими другими; но он и первого удара ее не вынес. Он женился рано, по любви; жена его, моя мать, умерла очень скоро; я остался после нее шести месяцев. Отец увез меня в деревню и целые двенадцать лет не выезжал никуда. Он сам занимался моим воспитанием и никогда бы со мной не расстался, если б брат его, мой родной дядя, не заехал к нам в деревню. Дядя этот жил постоянно в Петербурге и занимал довольно важное место. Он уговорил отца отдать меня к нему на руки, так как отец ни за что не соглашался покинуть деревню. Дядя представил ему, что мальчику моих лет вредно жить в совершенном уединении, что с таким вечно унылым и молчаливым наставником, каков был мой отец, я непременно отстану от моих сверстников, да и самый нрав мой легко может испортиться. Отец долго противился увещаниям своего брата, однако уступил наконец. Я плакал, расставаясь с отцом; я любил его, хотя никогда не видал улыбки на лице его. Но, попавши в Петербург, скоро позабыл наше темное и невеселое гнездо. Я поступил в юнкерскую школу, а из школы перешел в гвардейский полк. Каждый год приезжал я в деревню на несколько недель и с каждым годом находил отца моего всё более и более грустным, в себя углубленным, задумчивым до робости. Он каждый день ходил в церковь и почти разучился говорить. В одно из моих посещений (мне уже было лет двадцать с лишком) я в первый раз увидал у нас в доме худенькую черноглазую девочку лет десяти — Асю. Отец сказал, что она сирота и взята им на прокормление — он именно так выразился. Я не обратил особенного внимания на нее; она была дика, проворна и молчалива, как зверек, и как только я входил в любимую комнату моего отца, огромную и мрачную комнату, где скончалась моя мать и где даже днем зажигались свечки, она тотчас пряталась за вольтеровское кресло его или за шкаф с книгами. Случилось так, что в последовавшие за тем три, четыре года обязанности службы помешали мне побывать в деревне. Я получал от отца ежемесячно по короткому письму; об Асе он упоминал редко, и то вскользь. Ему было уже за пятьдесят лет, но он казался еще молодым человеком. Представьте же мой ужас: вдруг я, ничего не подозревавший, получаю от приказчика письмо, в котором он извещает меня о смертельной болезни моего отца и умоляет приехать как можно скорее, если хочу проститься с ним. Я поскакал сломя голову и застал отца в живых, но уже при последнем издыхании. Он обрадовался мне чрезвычайно, обнял меня своими исхудалыми руками, долго поглядел мне в глаза каким-то не то испытующим, не то умоляющим взором и, взяв с меня слово, что я исполню его последнюю просьбу, велел своему старому камердинеру привести Асю. Старик привел ее: она едва держалась на ногах и дрожала всем телом. — Вот, — сказал мне с усилием отец, — завещаю тебе мою дочь — твою сестру. Ты всё узнаешь от Якова, — прибавил он, указав на камердинера. Ася зарыдала и упала лицом на кровать. Полчаса спустя мой отец скончался. Вот что я узнал. Ася была дочь моего отца и бывшей горничной моей матери, Татьяны. Живо помню я эту Татьяну, помню ее высокую стройную фигуру, ее благообразное, строгое, умное лицо, с большими темными глазами. Она слыла девушкой гордой и неприступной. Сколько я мог понять из почтительных недомолвок Якова, отец мой сошелся с нею несколько лет спустя после смерти матушки. Татьяна уже не жила тогда в господском доме, а в избе у замужней сестры своей, скотницы. Отец мой сильно к ней привязался и после моего отъезда из деревни хотел даже жениться на ней, но она сама не согласилась быть его женой, несмотря на его просьбы. `
