export const robinsonR = `К этому времени Пятница научился понимать почти все, что я говорил ему. Сам он изъяснялся по-английски довольно бойко, хотя очень неправильно. Мало-помалу я рассказал ему всю свою жизнь: как я попал на мой остров, сколько лет прожил на нем и как провел эти годы. Еще раньше я открыл Пятнице тайну стрельбы из ружья (потому что для него это была действительно тайна): я показал ему пули, объяснил действие пороха и научил его стрелять. Я отдал в полное его распоряжение одно из своих ружей. Я подарил ему нож и этим подарком буквально осчастливил его. Я смастерил для него портупею, вроде тех, на каких у нас в Англии носят кортики; только вместо кортика я дал ему топор, который был, в сущности, таким же хорошим оружием и, кроме того, мог пригодиться для всяких хозяйственных надобностей. Я много рассказывал Пятнице о европейских странах, особенно о моей родине. Я описал ему нашу жизнь, наши обычаи, нравы, рассказал, как мы путешествуем по всем частям света и плаваем на больших кораблях. Я объяснил ему устройство большого парусного судна и рассказал кстати о том, как я ездил на корабль, потерпевший крушение, и показал ему издали место, где корабль наскочил на подводные камни. Конечно, я мог показать его весьма приблизительно, так как корабль давно разбило в щепки и все обломки унесло в море. Показал я ему также ту полусгнившую лодку, в которой мы хотели спастись, когда буря пригнала нас к этому берегу. После того как я устроил жилье для наших больных гостей, только что спасенных из плена, ввел их под крышу их нового дома, где они могли отдохнуть и восстановить свои силы, нужно было приготовить им еду. Я послал Пятницу в мое маленькое стадо за годовалым козленком. Заколов его, он, по моим указаниям, приготовил из козлятины жаркое и крепкий бульон. Этот бульон мы заправили ячменем и рисом; получился очень вкусный суп. Стряпня происходила за наружной стеной, ибо как сказано выше, я никогда не разводил огня внутри крепости. Мы накрыли стол в новой палатке и пообедали на новоселье вчетвером. Я председательствовал за этим обедом и занимал наших гостей разговорами. Пятница служил мне переводчиком, не только когда я говорил с его отцом, но и с испанцем, так как испанец довольно хорошо изъяснялся на языке дикарей. Когда мы пообедали, или, вернее, поужинали, я попросил Пятницу взять одну из пирог и съездить за нашими ружьями, которые за недосугом мы бросили на месте сражения; на другой день я послал его зарыть трупы убитых, а также ужасные остатки кровавого пиршества. Пятница в точности выполнил мое поручение. Он так тщательно уничтожил все следы дикарей, что, когда я снова побывал на том месте, я не сразу мог его узнать. Только по деревьям на опушке прибрежного леса я догадался, что людоеды пировали именно здесь. Через несколько дней, когда мои новые друзья отдохнули и немного пришли в себя после перенесенных ими испытаний, я начал при помощи Пятницы беседовать с ними. Прежде всего я спросил отца Пятницы, не боится ли он, что убежавшие людоеды могут вернуться на остров с целым полчищем других дикарей, которые жестоко расправятся с нами. Старик отвечал, что, по его мнению, убежавшие дикари никоим образом не могли добраться до родных берегов в такую сильную бурю, какая бушевала в ту ночь, что, наверное, их лодку опрокинуло и все они утонули. - А если они остались в живых, — сказал он, — их отнесло в сторону и прибило к земле враждебного им племени, где их непременно съедят. Помолчав немного, старик продолжал: - Но если даже они добрались благополучно домой, то и тогда они не рискнут воротиться. Они были так страшно напуганы вашим неожиданным нападением, грохотом и огнем выстрелов, что, вероятно, расскажут своим соплеменникам, будто товарищи их погибли от грома и молнии. Вас же двоих — тебя и Пятницу — они приняли за разгневанных дьяволов, сошедших на землю, чтобы их истребить. Я сам слышал, как они говорили об этом друг другу. Они не могут представить себе, чтобы простой смертный мог изрыгать пламя, говорить громами и убивать на дальнем расстоянии, даже не поднимая руки. Старик был прав. Впоследствии я узнал, что даже много лет спустя ни один дикарь не осмеливался показаться на моем острове. Очевидно, те беглецы, которых мы считали погибшими, все же вернулись на родину и своими страшными рассказами напугали других дикарей. Возможно даже, что в их племени сложилось поверье, будто всякого, кто ступит на берег этого волшебного острова, боги уничтожат огнем. Не предвидя этого, я долгое время был в постоянной тревоге, ожидая мести дикарей. Впрочем, и я и моя маленькая армия всегда были готовы сражаться: ведь нас было теперь четверо, и, явись к нам хоть сотня врагов, мы не побоялись бы в любое время вступить с ними в бой. Но ведь их могло быть и двести и триста, и тогда они победили бы нас. Однако дни проходили, а лодки дикарей не появлялись. Вместе с тем я все чаще и чаще возвращался к давнишней своей мечте о путешествии на материк. Отец Пятницы не раз уверял меня, что я могу смело рассчитывать на радушный прием у его земляков, так как я спас его сына и его самого от смерти. Но после одного серьезного разговора с испанцем я начал сомневаться, стоит ли приводить в исполнение мой план. Испанец сказал мне, что хотя дикари действительно приютили у себя семнадцать испанцев и португальцев, потерпевших крушение у их берегов, но все эти европейцы испытывают ныне крайнюю нужду и порою даже голодают. Дикари не притесняют их и дают им полную свободу, но сами живут так скудно, что не всегда могут прокормить новопришельцев. Я спросил испанца о подробностях их последнего плавания, и он сообщил мне, что их корабль шел из Рио-де-ла-Платы в Гавану, куда должен был доставить серебро и меха и нагрузиться европейскими товарами, имеющимися там в изобилии. Во время бури пять человек из их корабельной команды утонуло, а остальные после многодневных страданий и ужасов, изнуренные жаждой и голодом, наконец пристали к стране людоедов. Высадившись в этой стране, они испытывали отчаянный страх, так как с минуты на минуту ожидали, что их съедят дикари. У них было с собой огнестрельное оружие, но не было ни пороха, ни пуль: тот порох, который они взяли с собой в лодку, почти весь был подмочен в пути, а что осталось, они давно израсходовали, потому что первое время могли добывать себе пищу только охотой. Я спросил у него, какая, по его мнению, участь ожидает его товарищей в стране дикарей и пытались ли они когда-нибудь выбраться оттуда на волю. Он отвечал, что у них было много совещаний по этому поводу, но все кончалось слезами и жалобами. - Ведь у нас не было, — пояснил он, — ни судна, на котором можно пуститься в открытое море, ни инструментов для постройки подобного судна, ни съестных припасов. Тогда я сказал ему: - Я буду говорить с вами прямо. Как вы думаете, согласятся ли ваши товарищи переехать на мой остров? Я охотно пригласил бы их сюда. Мне кажется, что все сообща мы нашли бы способ добраться до какой-нибудь прибрежной страны, а оттуда — на родину. Одно только пугает меня: приглашая их сюда, я отдаю себя в их руки. Что, если они окажутся коварными, злыми людьми? Что, если за мое гостеприимство они отплатят мне изменой? Чувство благодарности, вообще говоря, некоторым людям совершенно не свойственно. Среди них могут оказаться предатели. А это было бы, согласитесь, слишком уж обидно: выручить людей из беды только для того, чтобы очутиться их пленником в Новой Испании. Уж лучше быть съеденным дикарями, чем попасть в беспощадные когти попов или быть сожженным инквизиторами. Если бы ваши товарищи, — продолжал я, — приехали сюда, я убежден, что при таком количестве работников нам ничего не стоило бы построить большое судно, на котором мы могли бы пробраться на юг и дойти до Бразилии или направиться к северу — до испанских владений. Но, разумеется, если я вложу им в руки оружие, а они в благодарность за мою доброту обратят это оружие против меня же, если, пользуясь тем, что они сильнее меня, они отнимут у меня мою свободу, — тогда они заставят меня пожалеть, что я сделал им столько добра. `
